import "./index.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";

import first from "../images/Rectangle (1).png";
import second from "../images/Rectangle (2).png";
import third from "../images/Rectangle (3).png";
import four from "../images/Rectangle (4).png";
import five from "../images/Rectangle (5).png";
import six from "../images/Rectangle (6).png";
import seven from "../images/Rectangle (7).png";
import eight from "../images/Rectangle (8).png";
import nine from "../images/Rectangle (9).png";
import ten from "../images/Rectangle (10).png";
import bottomPic from "../images/Vector (1).png";
export default function Furniture() {
  return (
    <section className="main">
      <div>
        <p className="furni_title">Landing Furnished</p>
        <div className="furni_descr">
          Enjoy all the amenities of a fully furnished Landing home
        </div>
        <Carousel style={{ width: "60%" }}>
          <div>
            <img className="car_img" alt="bed" src={first} />
          </div>
          <div>
            <img className="car_img" alt="sofa" src={second} />
          </div>
        </Carousel>
      </div>

      <p style={{ marginTop: "85px" }} className="furni_title">
        Every Landing includes
      </p>
      <div className="firni_collection">
        <div className="furni_card">
          <img src={third} alt="third" />
          <p>Comfy, in-house designed furnishings</p>
        </div>
        <div className="furni_card">
          <img src={four} alt="third" />
          <p>Luxury mattress and linens</p>
        </div>
        <div className="furni_card">
          <img src={five} alt="third" />
          <p>A fully-equipped kitchen</p>
        </div>
        <div className="furni_card">
          <img src={six} alt="third" />
          <p>Dedicated workspaces</p>
        </div>
        <div className="furni_card">
          <img src={seven} alt="third" />
          <p>Smart TV + Wifi</p>
        </div>
        <div className="furni_card">
          <img src={eight} alt="third" />
          <p>Premium toiletries</p>
        </div>
        <div className="furni_card">
          <img src={nine} alt="third" />
          <p>Plenty of storage</p>
        </div>
        <div className="furni_card">
          <img src={ten} alt="third" />
          <p>Comfy, in-house designed furnishings</p>
        </div>
      </div>

      <img className="bottomPic" alt="bottomPic" src={bottomPic} />
    </section>
  );
}
