import first from "../images/Frame (3).png";
import second from "../images/Frame (4).png";
import third from "../images/Frame (5).png";
import four from "../images/Rectangle (11).png";
import five from "../images/Rectangle (12).png";
import "bootstrap/dist/css/bootstrap.min.css";
import { Carousel } from "react-bootstrap";
export default function Photos() {
  return (
    <div>
      <div className="photo_titles">
        <p className="title">See where members love to live</p>
        <p>
          We offer over 80 cities, and counting, across the U.S. and encourage
          you to explore with our free, 7-day travel benefit.
        </p>
        <Carousel
          prevLabel=""
          nextLabel=""
          indicators={false}
          keyboard={false}
          pauseOnHover={true}
        >
          <Carousel.Item style={{ height: "300px" }}>
            <img
              style={{ height: "300px" }}
              className="d-block  photo-img"
              id="photo-img"
              src={first}
            />
          </Carousel.Item>

          <Carousel.Item style={{ height: "300px" }}>
            <img
              style={{ height: "300px" }}
              id="photo-img"
              className="d-block  photo-img"
              src={second}
            />

            <img
              style={{ height: "300px" }}
              id="photo-img"
              className="d-block  photo-img"
              src={second}
            />
          </Carousel.Item>

          <Carousel.Item style={{ height: "300px" }}>
            <img
              style={{ height: "300px" }}
              className="d-block photo-img"
              id="photo-img"
              src={third}
            />
          </Carousel.Item>

          <Carousel.Item style={{ height: "300px" }}>
            <img
              style={{ height: "300px" }}
              className="d-block photo-img"
              id="photo-img"
              src={four}
            />
          </Carousel.Item>

          <Carousel.Item style={{ height: "300px" }}>
            <img
              style={{ height: "300px" }}
              className="d-block photo-img"
              id="photo-img"
              src={five}
            />
          </Carousel.Item>
        </Carousel>
      </div>
    </div>
  );
}
